.product-details {
  margin-top: 100px !important;
  transition: margin-top 0.3s ease;
}

.product-details-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.product-details-info h6 {
  margin-bottom: 20px !important;
  font-weight: bold;
  color: #333;
}

.similar-products .card {
  background-color: rgba(128, 128, 128, 0.097);
  width: 18rem;
  transition: transform 0.3s ease;
}

.similar-products .card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.similar-products .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.similar-products .card-price {
  color: green;
  font-weight: bold;
  font-size: 16px;
}

.similar-products button {
  border-radius: 0;
  width: 100%;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
  background-color: #434343;
  color: white;
  padding: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  border: none;
}

.similar-products button:hover {
  background-color: #333;
}

.similar-products .card-text {
  color: rgb(90, 89, 89);
  font-size: 14px;
  margin-top: 10px;
  line-height: 1.5;
}

.similar-products .card img {
  transition: transform 0.3s ease;
}

.similar-products .card img:hover {
  transform: scale(1.05);
}

.similar-products .card-img-top {
  height: 300px !important;
  object-fit: cover;
}