@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* General Styles */
body {
  font-family: "Poppins", sans-serif;
  line-height: 1.6;
  scroll-behavior: smooth;
  transition: background-color 0.3s ease;
}

a {
  transition: color 0.3s ease;
}

/* Navbar Styles */
.navbar {
  font-size: 17px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  --webkit-box-shadow: 0 8px 6px -6px gray;
  border-bottom: solid gray !important;
  background-color: #ffffff;
  position: sticky;
  top: 0;
  z-index: 100;
}

.nav-link {
  font-weight: 300 !important;
  color: #333;
  padding: 15px;
}

.nav-link:hover {
  color: #000;
}

.active {
  border-bottom: 2px solid #000;
}

.navbar-brand {
  font-weight: 700;
  font-family: "roboto", sans-serif;
  letter-spacing: 3px;
  color: #333;
}

.search-form {
  margin-right: 10px;
  margin-top: 4px;
}

.search-form input {
  border: none;
  border-radius: 0;
  padding: 5px;
  width: 200px;
}

.search-form button {
  background-color: #000000;
  border-radius: 0;
  color: white;
  padding: 5px;
  cursor: pointer;
}

/* Page Not Found Styles */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
  color: #333;
  animation: fadeIn 1s ease-in-out;
}

.pnf-heading {
  font-weight: normal;
  color: #666;
  animation: fadeIn 1s ease-in-out 0.2s forwards;
}

.pnf-btn {
  color: #333;
  border: 1px solid #333;
  text-decoration: none;
  padding: 10px 20px;
  margin-top: 10px;
  display: inline-block;
  animation: fadeIn 1s ease-in-out 0.4s forwards;
}

.pnf-btn:hover {
  background-color: #333;
  color: #fff;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/* Footer Styles */
.footer {
  color: white;
  padding: 25px;
  background: #000000; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #434343,
    #000000
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #434343, #000000);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.footer a {
  text-decoration: none;
  color: white;
  padding: 10px;
  margin: 5px;
}

.footer a:hover {
  color: #ffefba;
  border-bottom: 1px solid #ffefba;
}

/* Contact Us Styles */
.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

/* Product Link Styles */
.product-link {
  text-decoration: none !important;
  color: black !important;
}

.cat-btn {
  padding: 40px 0px;
  font-size: 24px;
  text-transform: uppercase;
  background-color: #434343;
  color: white;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.cat-btn:hover {
  background-color: #333;
}

/* Dashboard Styles */
.dashboard {
  margin-top: 100px !important;
}

.dashboard-menu h4 {
  background-color: #434343 !important;
  color: white;
  padding: 20px 0px;
}