/* Banner Image */
.banner-img {
  margin-top: 60px;
}

/* Home Page */
.home-page {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.home-page .card {
  background-color: rgba(128, 128, 128, 0.097);
  width: 18rem;
  margin: 1rem;
  transition: transform 0.3s ease;
}

.home-page .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
}

.home-page .card-price {
  color: green;
  font-weight: bold;
}

.home-page button {
  border-radius: 0;
  width: 100%;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
  background-color: green;
  color: white;
  padding: 0.5rem;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.home-page button:hover {
  background-color: darkgreen;
}

.home-page .card-text {
  color: rgb(90, 89, 89);
  padding: 0 1rem;
}

.home-page .card img:hover {
  transform: scale(0.9);
}

.home-page .card-img-top {
  height: 300px !important;
  object-fit: cover;
}

/* Filters */
.filters h4 {
  margin-top: 100px;
  color: gray !important;
  border-bottom: 1px solid rgba(128, 128, 128, 0.337);
  text-align: center;
}

.ant-checkbox-wrapper:first-of-type {
  margin-left: 8px;
}

.ant-checkbox-wrapper {
  margin: 2px;
}

.ant-radio-wrapper {
  margin-bottom: 5px !important;
  margin-left: 10px;
}

.filters button {
  background-color: black;
  width: 100%;
  border: none;
  border-radius: 0;
  margin-top: 20px;
  padding: 0.5rem;
  font-size: 16px;
  font-weight: bold;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.filters button:hover {
  background-color: #222;
}

.loadmore {
  color: green;
  font-weight: bold;
  font-size: 20px !important;
  text-align: center;
  display: block;
  margin: 2rem auto;
}